html {
  /*rem 和 em*/
  /*em 是相对于父元素的 font-size*/
  /*rem 是相对于根元素的 font-size, r 就是 root 的意思*/
  /*浏览器默认的 font-size 是 16px，所以 16 x 62.5% = 10px*/
  font-size: 62.5%;
}

/*viewport height === vh*/
/*viewport 就是视口的意思，用户页面的可视区域就是一个视口*/
/*1vh 就是 1% 的视口高度，100vh 就是整个视口*/
html body #root .App {
  min-height: 100vh;
}
